.current-weather-container {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 40vh;
  box-sizing: border-box;
}

.current-weather-icon {
  width: 500px;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.current-weather-icon .icon {
  font-size: 200px;
}

.current-weather {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 20px;
}

.current-weather > * {
  margin-bottom: 15px;
}

.current-weather-temp {
  font-size: 96px;
  font-weight: 500;
}

.current-weather-status {
  font-size: 64px;
  font-weight: 500;
}

.current-weather-date {
  font-size: 24px;
  font-weight: 300;
}

@media (max-width: 1024px) {
  .current-weather-container {
    flex-direction: column;
    align-items: flex-start;
    height: fit-content;
  }

  .current-weather-icon {
    width: 100vw;
    height: fit-content;
    margin: 20px 0;
  }

  .current-weather-icon .icon {
    font-size: 150px;
  }

  .current-weather-temp {
    font-size: 64px;
  }

  .current-weather-status {
    font-size: 48px;
  }

  .current-weather-date {
    font-size: 20px;
  }
}
